@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i);
.ali-logo {
  width: 38px;
  height: 32px;
}
.social-media-icons .social-media-icon {
  fill: #babcc5;
}
.social-media-icons a {
  margin: 0 10px;
}
.header {
  background-color: #1d1e2b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 72px;
  color: #babcc5;
}
.header a {
  color: #babcc5;
}
.header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  margin-left: 150px;
  font-size: 21px;
  font-weight: bold;
}
.header ul li:not(:first-of-type) {
  border-left: 1px solid #babcc5;
}
.header ul li {
  padding: 0 30px;
  display: flex;
  align-items: center;
}
button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 45px;
  background-color: #ff7250;
  color: #ffffff;
  font-size: 22px;
  font-style: bold;
  border: 1px solid;
  border-radius: 10px;
  margin-top: 40px;
}
button:hover {
  background-color: #f15a35;
  cursor: pointer;
}
.intro {
  display: flex;
  flex: 1 1;
  justify-content: space-around;
  margin-top: 50px;
  padding-bottom: 120px;
  background-repeat: no-repeat;
  background-image: url(/personal-website/static/media/BackgroundShape.9fc5c087.svg);
  background-size: cover;
}

.main_image {
  min-width: 432px;
  height: 432px;
  margin: 0 100px 50px 200px;
}

.intro_text {
  margin-right: 200px;
}

.intro-title {
  font-size: 96px;
  margin: 0 0 40px 0;
  font-weight: bold;
}
.footer {
  background-color: #1d1e2b;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  color: #babcc5;
}
.footer .copyright {
  display: flex;
  align-items: center;
}
.footer .copyright > span {
  margin-left: 20px;
}
.work-item {
  text-align: center;
  margin: 15px 50px 0 50px;
  min-width: 200px;
}
.work-item h4 {
  font-size: 26px;
  margin: 25px 0 15px 0;
}
.my-work {
  margin: 0 0 50px 0;
  height: 620px;
  max-width: 100%;
  min-width: 939px;
  background-repeat: no-repeat;
  background-image: url(/personal-website/static/media/BlueGradientBackground.90564c2e.svg);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
}
.my-work h1 {
  margin: 50px 0 60px 0;
}
.my-work .work-items {
  display: flex;
  justify-content: space-around;
  color: #f2f7f9;
  margin: 0 50px;
}
.get-in-touch {
  display: flex;
  margin: 50px 0 50px 0;
}
.get-in-touch .contact-text {
  margin: 0 0 0 300px;
}
.get-in-touch .contact-text p {
  margin: 40px 0 40px 0;
}
.get-in-touch .contact-image {
  height: 374px;
  margin: 0 230px 0 200px;
}
.get-in-touch .contact-image img {
  height: 100%;
  border-radius: 50%;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Lato, sans-serif;
  font-size: 19px;
  color: #1d1e2b;
  max-width: 100%;
  min-width: 800px;
}

#root {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

button {
  outline: none;
}

a {
  text-decoration: none;
}

.logo-path {
  fill: #babcc5;
}
