.intro {
  display: flex;
  flex: 1;
  justify-content: space-around;
  margin-top: 50px;
  padding-bottom: 120px;
  background-repeat: no-repeat;
  background-image: url('../assets/images/BackgroundShape.svg');
  background-size: cover;
}

.main_image {
  min-width: 432px;
  height: 432px;
  margin: 0 100px 50px 200px;
}

.intro_text {
  margin-right: 200px;
}

.intro-title {
  font-size: 96px;
  margin: 0 0 40px 0;
  font-weight: bold;
}
